<template>
    <v-app>
        <!-- Top bar -->
        <v-toolbar color="primary">
            <!-- Title -->
            <v-toolbar-title class="headline text-uppercase"> Gastos de Vocy </v-toolbar-title>

            <!-- Save button form -->
            <save-button :itemsList="itemsList"></save-button>
        </v-toolbar>

        <!-- Total info -->
        <v-card align='end' elevation='0' class='mt-5 mr-2' color='primary' variant='text'>
            <v-card-text style='font-size: 110%'>
                <strong>
                    Total: &nbsp; R$ {{ this.sumValues }}
                </strong>
            </v-card-text>
        </v-card>

        <!-- Main table -->
        <v-card align="center" elevation="0" class="ma-15">
            <!-- Table header -->
            <v-row>
                <v-col> <strong> Data </strong> </v-col>
                <v-col align="start"> <strong> Descrição </strong> </v-col>
                <v-col align="start"> <strong> Valor </strong> </v-col>
                <v-col> </v-col>
            </v-row>

            <!-- Thin horizontal line -->
            <v-divider class="mt-2 mb-4"></v-divider>

            <!-- Table main content: one row for each item in itemsList -->
            <item-row 
                v-for = "item in itemsList"
                :key = "item.id"
                :id = "item.id"
                :data = "item.date"
                :descricao = "item.desc"
                :valor = "item.value"
                @delete-item = "deleteItem(item.id)"
                @edit-item = "editItem(item.id, $event)"
            ></item-row>

            <!-- Buttons at the bottom of the page: add item and delete all -->
            <v-row class="mt-15">
                <!-- Empty first column -->
                <v-col> </v-col>
                
                <!-- Second column: add item form -->
                <v-col>
                    <!-- Button -->
                    <v-btn @click="showAddItemForm=true" color="primary"> Adicionar item </v-btn>
                    <!-- Popup form -->
                    <add-item-form v-model="showAddItemForm" @add-item="addItem($event)" @close-form="showAddItemForm=false" />
                </v-col>                
                
                <!-- Third column: delete all button -->
                <v-col align="start">
                    <v-btn @click="removeAll" color="#d33">Apagar tudo</v-btn>
                </v-col>
                
                <!-- Empty last column -->
                <v-col> </v-col>
            </v-row>
        </v-card>
    </v-app>
</template>

<script>
    /* Imported libraary */
    import axios from "axios"

    /* Imported components */
    import AddItemForm from "./components/AddItemForm.vue"
    import ItemRow from "./components/ItemRow.vue"
    import SaveButton from "./components/SaveButton.vue"
    
    /* Export Defaut is in every Vue file */
    export default {
        /* Imported components */
        components: { AddItemForm, ItemRow, SaveButton },

        /* Local data used in this component */
        data () {
            return {
                itemsList: null,
                showAddItemForm: false,
            }
        },

        /* Actions to be taken when the component is created */
        created () {
            /* Fetch JSON data from Python Any Where */
            const url = "https://tmacedo.pythonanywhere.com/getjson/gastos_vocy.json";
            axios.get(url).then((response) => {
                this.itemsList = this.sortListByDate(JSON.parse(response.data.message));
            });
        },

        /* Functions used in this component */
        methods: {

            /* Add item to the list of items (itemsList) */
            addItem (stuff) {
                const uniqueId = "gasto-" + Date.now().toString();
                this.itemsList.push({ id: uniqueId, date: stuff[0].toString(), desc: stuff[1].toString(), value: Number(stuff[2]) });
                this.itemsList = this.sortListByDate(this.itemsList);
                this.showAddItemForm = false
            },

            /* Delete item from the list of items */
            deleteItem (stuff) {
                const indexToDelete = this.itemsList.findIndex((item) => item.id === stuff);
                this.itemsList.splice(indexToDelete, 1)
            },

            /* Edit item in the list of items */
            editItem (thing, stuff) {
                this.deleteItem(thing);
                this.addItem(stuff)
            },

            /* Remove all items from the list of items */
            removeAll () {
                if (confirm("Tem certeza que você quer apagar tudo?")) {
                    this.itemsList = { }
                }
            },

            /* Sort the list of items by date */
            sortListByDate (lista) {
                lista.sort(function (a, b) {
                    return new Date(a.date) - new Date(b.date);
                });
                return lista
            },
        },

        /* Data that is computed from other data within this component */
        computed: {
            /* Sum the values of all the items in the list of items */
            sumValues () {
                var s = Number(0);
                for (const item in this.itemsList) {
                    s += Number(this.itemsList[item].value);
                }
                s = (Math.round(s * 100) / 100).toFixed(2);
                return s
            }
        },
    }
</script>
