<template>
    <!-- Inicia uma janela de popup com o formulario -->
    <v-dialog width="1024">

        <!-- Conteudo da janela-formulario -->
        <v-card>
            <!-- Titulo da janela -->
            <v-card-title>
                <span class="text-h5">Adicionar gasto</span>
            </v-card-title>

            <!-- Formulario -->
            <v-card-text>
                <v-row align-content="space-evenly">
                    <!-- Date field -->
                    <v-col>
                        <v-text-field 
                            label="Data"
                            type="Date"
                            v-model.lazy.trim="date"
                        />
                    </v-col>

                    <!-- Description field -->
                    <v-col>
                        <v-text-field
                            label="Descrição"
                            prepend-inner-icon="mdi-pencil"
                            v-model.lazy.trim="desc"
                        />
                    </v-col>

                    <!-- Value field -->
                    <v-col>
                        <v-text-field 
                            label="Valor"
                            prepend-inner-icon="mdi-cash"
                            prefix="R$ "
                            type="number"
                            min="0"
                            step=".01"
                            v-model.lazy.number="value"
                        />
                    </v-col>
                </v-row>
            </v-card-text>

            <!-- Add and cancel buttons -->
            <v-card-actions>
                <v-spacer></v-spacer>

                <v-btn 
                    color="blue-darken-1"
                    variant="text"
                    @click.prevent="sendAddItem()"
                >
                    Adicionar
                </v-btn>

                <v-btn 
                    color="blue-darken-1"
                    variant="text"
                    @click="this.$emit('close-form')"
                >
                    Cancelar
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
    export default {
        methods: {
            sendAddItem () {
                if (this.date === '' || this.desc === '' || this.value === '') { return; }
                this.$emit('add-item', [this.date, this.desc, this.value]);
                this.date = '';
                this.desc = '';
                this.value = '';
            }
        },

        data () { 
            return { 
                date: '',
                desc: '',
                value: '',
            } 
        }
    }
</script>
