<template>
    <v-toolbar-items>
        <form 
            action="https://tmacedo.pythonanywhere.com/setjson/gastos_vocy.json"
            method="post"
            enctype="application/json"
        >
            <!-- Hidden input used to send the current data -->
            <input type="hidden" id="hiddenInput" name="jsondata"/>

            <!-- Save button: on click, send data -->
            <v-btn type="submit" @click='sendData(itemsList)'> 
                <v-icon size="275%" class="mt-3">mdi-content-save-alert</v-icon>
            </v-btn>
        </form>
    </v-toolbar-items>
</template>

<script>
    export default {
        props: {
            itemsList: { required: true, type: Object }
        },

        methods: {
            sendData (stuff) {
                const outData = JSON.stringify(stuff);
                document.getElementById('hiddenInput').value = outData;
            }
        }
    }
</script>
