<template>
    <v-row>
        <!-- First column: date -->
        <v-col> {{ formatDate(data) }} </v-col>

        <!-- Second column: description -->
        <v-col align="start"> {{ descricao }} </v-col>

        <!-- Third column: value -->
        <v-col align="start"> {{ formatValue(valor) }} </v-col>

        <!-- Last column: edit and delete buttons -->
        <v-col align="start">
            <!-- Edit button: on click, edit this item -->
            <v-btn @click="showEditItemForm=true" elevation="0" class="pa-0 ml-2">
                <v-icon color="primary">mdi-square-edit-outline</v-icon>
            </v-btn>
            <edit-item-form
                :data="data"
                :descricao="descricao"
                :valor="valor"
                v-model="showEditItemForm"
                @edit-item="sendEditItem($event)"
                @close-form="showEditItemForm=false"
            />

            <!-- Delete button: on click, delete this item -->
            <v-btn @click="this.$emit('delete-item')" elevation="0" class="pa-0 ml-2">
                <v-icon color="primary">mdi-delete</v-icon>
            </v-btn>
        </v-col>                
    </v-row>
</template>

<script>
    import EditItemForm from "./EditItemForm.vue"

    export default {
        components: { EditItemForm },

        props: {
            id: { required: true, type: String },
            data: { required: true, type: String },
            descricao: { required: true, type: String },
            valor: { required: true, type: Number }
        },
        
        data () {
            return {
                showEditItemForm: false
            };
        },
        
        methods: {
            formatDate (texto) {
                var lista = texto.split("-");
                lista.reverse();
                const saida = lista.join("/");
                return saida;
            },
            formatValue (numero) {
                numero = Math.round(numero * 100) / 100;
                var string = numero.toFixed(2);
                string = "R$ " + string;
                return string;
            },
            sendEditItem (stuff) {
                this.$emit('edit-item', stuff);
                this.showEditForm = true
            }
        }
    }
</script>
