<template>
    <!-- Inicia uma janela de popup com o formulario -->
    <v-dialog width="1024">

        <!-- Conteudo da janela-formulario -->
        <v-card>
            <!-- Titulo da janela -->
            <v-card-title>
                <span class="text-h5">Editar gasto</span>
            </v-card-title>

            <!-- Formulario -->
            <v-card-text>
                <v-row align-content="space-evenly">
                    <!-- Date field -->
                    <v-col>
                        <v-text-field 
                            label="Data"
                            type="Date"
                            v-model.lazy.trim="newDate"
                        />
                    </v-col>

                    <!-- Description field -->
                    <v-col>
                        <v-text-field
                            label="Descrição"
                            prepend-inner-icon="mdi-pencil"
                            v-model.lazy.trim="newDesc"
                        />
                    </v-col>

                    <!-- Value field -->
                    <v-col>
                        <v-text-field 
                            label="Valor"
                            prepend-inner-icon="mdi-cash"
                            prefix="R$ "
                            type="number"
                            min="0"
                            step=".01"
                            v-model.lazy.number="newValue"
                        />
                    </v-col>
                </v-row>
            </v-card-text>

            <!-- Edit and cancel buttons -->
            <v-card-actions>
                <v-spacer></v-spacer>

                <v-btn 
                    color="blue-darken-1"
                    variant="text"
                    @click.prevent="sendEditItem()"
                >
                    Editar
                </v-btn>

                <v-btn 
                    color="blue-darken-1"
                    variant="text"
                    @click="this.$emit('close-form')"
                >
                    Cancelar
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
    export default {
        props: {
            data: { required: true, type: String },
            descricao: { required: true, type: String },
            valor: { required: true, type: Number }
        },

        methods: {
            sendEditItem () {
                if (this.newDate !== '' && this.newDesc !== '' && this.newValue !== '') {
                    this.$emit('edit-item', [this.newDate, this.newDesc, this.newValue])
                }
            }
        },

        data () { 
            return {
                newDate: this.data,
                newDesc: this.descricao,
                newValue: this.valor
            } 
        }
    }
</script>
